@media (max-width: 480px) {

    .small {
        display: none;
    }
}

@media (max-width: 768px) {

    .middle {
        display: none;
    }
}

@media (max-width: 1025px) {}

@media (max-width: 1200px) {

    /* стили для md-устройств */
    .nav {
        display: grid;
        height: 100%;
        /* margin-left: 25px; */
        /* margin-right: 25px; */
        justify-content: end;
        align-items: center;
        grid-template-columns: repeat(2, auto);
    }
}

@media (min-width: 1200px) {

    /* стили для lg-устройств */
    .nav {
        display: grid;
        height: 100%;
        /* margin-left: 25px; */
        margin-right: 25px;
        justify-content: center;
        align-items: center;
        grid-template-columns: repeat(2, auto);
    }

}

@media (min-width: 1430px) {

    /* стили для lg-устройств */
    .nav {
        display: grid;
        height: 100%;
        /* margin-left: 25px; */
        margin-right: 25px;
        justify-content: center;
        align-items: center;
        grid-template-columns: repeat(2, auto);
    }

}