@media (max-width: 1200px) {
    .nav {
        display: none;
    }
}

@media (min-width: 1200px) {
    .nav {
        display: grid;
        height: 100%;
        /* margin-left: 25px;
    margin-right: 25px; */
        justify-content: center;
        align-items: center;
        grid-template-columns: repeat(8, auto);
    }
}