@media (max-width: 768px) {
    .imgBox img {
        display: flex;
        /* max-height: 600px; */
        width: 90vw;
        /* object-fit: cover; */
        object-fit: contain;
        /* margin-top: 210px; */
        z-index: 4;
        /* margin-top: 0;
        margin-left: 0; */
        align-content: start;
    }
}

@media (min-width: 768px) {
    .imgBox img {
        display: grid;
        grid-column: 2/3;
        grid-row: 1/3;
        height: 1100px;
        object-fit: cover;
        align-content: end;
        margin-top: -100px;
        margin-left: 80px;
        z-index: 4;
    }
}