.content {
    margin: 17px auto;
    display: grid;
    /* grid-row: 2fr 1fr; */
    text-align: center;
    max-width: 1200px;
    padding-left: 10px;
    padding-right: 10px;

}

.text {
    display: grid;
    margin-top: 30px;
    grid-row-gap: 30px;
    text-align: justify;
}



@media (max-width: 1200px) {
    .text_ul {
        padding-left: 10px;
        padding-right: 10px;
        text-align: justify;

    }
}

@media (min-width: 1200px) {
    .text_ul {
        padding-left: 0px;
        padding-right: 0px;
        text-align: justify;
    }
}