@media (max-width: 480px) {
  .small {
    display: none;
  }

  .middle {
    display: none;
  }

  .large {
    display: none;
  }

  .footer {
    flex: 0 0 auto;
    z-index: 2;
    width: 100%;
    /* position: fixed; */
    bottom: 0;
    left: 0;
  }
}

@media (min-width: 480px) and (max-width: 1025px) {
  .small {
    display: none;
  }

  .large {
    display: none;
  }

  /* .fakeBox {
    height: 520px;
    content: '';
  } */

  /* .footer {
    flex: 0 0 auto;
    z-index: 1;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
  } */
  .footer {
    flex: 0 0 auto;
    z-index: 2;
    width: 100%;
    /* position: fixed; */
    bottom: 0;
    left: 0;
  }
}

@media (min-width: 1025px) {
  .small {
    display: none;
  }

  .middle {
    display: none;
  }

  /* .fakeBox {
    height: 280px;
    content: '';
  } */

  .footer {
    flex: 0 0 auto;
    z-index: 1;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
  }
}


























