.sliderImg {
    width: 100%;
    height: 390px;
    /* height: 100%; */
    /* margin-top: 90px; */
    /* height: 500px; */
    margin-top: 7px;
    /* margin-bottom: 10px; */
    position: absolute;
    pointer-events: none;
    animation: moveImage 120s infinite linear;
    background-repeat: repeat-x;
    background-size: contain;

    background-image: url(./../../../../../public/3in1.svg);
    /* fill: black; */
    z-index: 1;
    /* background-color: #ECEEF2; */
    /* object-fit: cover; */
}

@keyframes moveImage {
    0% {
        background-position: 0 center;
    }

    100% {
        background-position: -10000px center;
    }
}

@media (max-width: 1410px) {
    .sliderImg {
        background-size: cover;
    }
}