/* Стили для кнопки */
.dropbtn {
    background-color: #ffffff;
    display: grid;
    margin-left: 0px;
    margin-right: 0px;
    border: none;
    height: 90px;
    width: 90px;
    align-items: center;
    /* background-color: #4CAF50; */
    /* color: white; */
    /* padding: 10px; */
    /* font-size: 16px; */
    /* width: 100%; */
    /* justify-items: center; */
    /* justify-items: center; */
}

.dropbtn:hover svg {
    fill: #AE0018;
}

.dropbtn svg {
    display: grid;
    width: 17px;
    height: 22px;
    margin-left: 15px;
    margin-right: 15px;
    align-self: center;
    grid-row: 1;
    grid-column: 1/2;
    justify-self: center;


}

.dropbtn:hover .btnBorder {
    opacity: 1;
    transform: scaleX(1) scaleY(1);
}

.btnBorder,
.btnBorderActive {
    display: grid;
    grid-row: 1;
    grid-column: 1/2;
    align-self: end;
    margin-left: 4px;
    margin-right: 4px;
    height: 3px;
    content: ' ';
    background-color: #AE0018;
    border-radius: 2px 2px 0 0;

}

.btnBorderActive {
    opacity: 1;
}

.btnBorder {
    opacity: 0;
    transition: opacity .2s ease-in-out, transform .2s ease-in-out;
    transform: scaleX(0.3) scaleY(0);
}

/* Стили для контейнера с выпадающим контентом */
.dropdown {
    position: relative;
    /* display: inline-block; */
    display: grid;
    justify-items: end;
    grid-column: 3;
}

/* Стили для выпадающего контента */
.dropdown_content {
    margin-top: 90px;
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    /* min-width: 300px; */
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    z-index: 1;
}

/* Стили для ссылок в выпадающем контенте */
.dropdown_content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    font-size: 22px;
    display: block;
}

.dropdown_content a.activeLink {
    color: #ae0018;
}

/* Стили для ссылок в выпадающем контенте при наведении */
.dropdown_content a:hover {
    background-color: #f1f1f1;
    color: #ae0018;
}

/* Показываем выпадающий контент, когда кнопка нажата */
.dropdown:hover .dropdown_content {
    display: block;
}

/* Стили для выпадающего контента, когда он активен */
.dropdown:focus-within .dropdown_content {
    display: block;
}

@media (min-width: 1200px) {
    .dropbtn {
        display: none;
    }
}

@media (max-width: 320px) {
    .dropbtn {
        background-color: #ffffff;
        display: grid;
        margin-left: 0px;
        margin-right: 0px;
        border: none;
        height: 90px;
        width: 70px;
        align-items: center;
        justify-self: center;
    }
}

@media (max-width: 480px) {
    .dropdown_content {
        /* margin-top: 93px; */
        width: 100vw;
        /* margin-right: 30px; */
    }
}

@media (min-width: 480px) {
    .dropdown_content {
        min-width: 300px;
    }

    .small {
        display: none;
    }
}

@media (min-width: 768px) {
    .middle {
        display: none;
    }
}