    .itemHead {
        word-wrap: break-word;
    }

    .img img {
        height: 222px;
        width: 410px;
        object-fit: contain;
    }

    @media (max-width: 480px) {
        .content {
            display: grid;
            grid-template-columns: auto;
            grid-template-rows: auto auto auto;
            row-gap: 20px;
            align-items: center;
            margin: 0 15px 0 15px;
        }

        .itemHead {
            display: grid;
            grid-row: 1/2;
            grid-column: 1/2;
            justify-content: start;
            text-align: start;
        }

        .itemText {
            display: grid;
            grid-row: 2/3;
            grid-column: 1/2;
            font-size: 24px;
            font-weight: 300;
            width: 100%;
            text-align: justify;
        }

        .img {
            display: grid;
            grid-row: 3;
            grid-column: 1;
            align-content: center;
            justify-content: center;
            /* margin-right: 30px; */
        }

        .img img {
            height: 222px;
            width: 90vw;
            object-fit: contain;
            justify-self: center;
        }
    }

    @media (min-width: 480px) and (max-width: 1025px) {
        .content {
            display: grid;
            grid-template-columns: auto;
            grid-template-rows: auto auto auto;
            row-gap: 20px;
            align-items: center;
            margin: 0 25px 0 25px;
        }

        .itemHead {
            display: grid;
            grid-row: 1/2;
            grid-column: 1/2;
            justify-content: start;
            text-align: start;
        }

        .itemText {
            display: grid;
            grid-row: 2/3;
            grid-column: 1/2;
            font-size: 24px;
            font-weight: 300;
            /* width: 100%; */
            text-align: justify;
        }

        .img {
            display: grid;
            grid-row: 3/4;
            grid-column: 1/2;
            align-content: center;
            justify-self: center;
            /* margin-right: 30px; */
        }
    }

    @media (min-width: 1025px) and (max-width: 1200px) {
        .content {
            display: grid;
            grid-template-columns: auto auto;
            grid-template-rows: auto auto;
            row-gap: 20px;
            align-items: center;
            margin: 0 25px 0 25px;
        }

        .itemHead {
            display: grid;
            grid-row: 1/2;
            grid-column: 2/3;
            justify-content: center;
        }

        .itemText {
            display: grid;
            grid-row: 2/3;
            grid-column: 2/3;
            font-size: 24px;
            font-weight: 300;
            width: 100%;
            text-align: justify;
        }

        .img {
            display: grid;
            grid-row: 2/3;
            grid-column: 1/2;
            align-content: center;
            margin-right: 30px;
        }

    }

    @media (min-width: 1200px) {
        .content {
            display: grid;
            grid-template-columns: auto auto;
            grid-template-rows: auto auto;
            row-gap: 10px;
            align-items: center;
        }

        .itemHead {
            display: grid;
            grid-row: 1/2;
            grid-column: 1/2;
            justify-content: center;
        }

        .itemText {
            display: grid;
            grid-row: 2/3;
            grid-column: 1/2;
            font-size: 24px;
            font-weight: 300;
            width: 100%;
            text-align: justify;
        }

        .img {
            display: grid;
            grid-row: 2/3;
            grid-column: 2/3;
            align-content: center;
            margin-left: 30px;
        }


    }

