@media (max-width: 1200px) {
    .content {
        display: grid;
        width: auto;
        /* padding-left: 15px;
        padding-right: 15px; */
        /* margin-right: auto;
        margin-left: auto; */
        justify-items: start;
        text-align: justify;
        color: #161616;
    }
}

@media (min-width: 1200px) {
    .content {
        display: grid;
        max-width: 1180px;
        margin-right: auto;
        margin-left: auto;
        justify-items: start;
        text-align: justify;
        color: #161616;
    }
}