@media (min-width: 1199px) {
    .item {
        display: grid;
        width: 100%;
        text-transform: uppercase;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        font-size: 14px;
    }

    .item:hover .itemBorder {
        opacity: 1;
        transform: scaleX(1) scaleY(1);
    }

    .itemBorder,
    .itemBorderActive {
        display: grid;
        grid-row: 1;
        grid-column: 1;
        align-self: end;
        margin-left: 4px;
        margin-right: 4px;
        height: 3px;
        content: ' ';
        background-color: #AE0018;
        border-radius: 2px 2px 0 0;

    }

    .itemBorderActive {
        opacity: 1;
    }

    .itemBorder {
        opacity: 0;
        transition: opacity .2s ease-in-out, transform .2s ease-in-out;
        transform: scaleX(0.3) scaleY(0);
    }

    .item a {
        display: grid;
        grid-row: 1;
        grid-column: 1;
        align-content: center;
        justify-content: center;
        margin-left: 5px;
        margin-right: 5px;
        height: 90px;
        color: #000000;
    }

    .item a.activeLink,
    .item a:hover {
        color: #AE0018;
    }
}

@media (min-width: 1330px) {
    .item {
        font-size: 16px;
    }

    .item a {
        margin-left: 10px;
        margin-right: 10px;
    }
}