@media (max-width: 480px) {
    .content {
        display: grid;
        grid-template-rows: repeat (3, 1fr);
        align-self: start;
        justify-items: start;
        padding-top: 20px;
        font-size: 22px;
        text-align: start;
    }

    .fakeBox {
        display: none;
    }
}

@media (min-width: 480px) and (max-width: 1200px) {
    .content {
        width: 100%;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: auto auto;
        column-gap: 30px;
        align-self: start;
        justify-items: start;
        padding-top: 20px;
        font-size: 22px;
        text-align: start;
    }

    /* .fakeBox {
        display: grid;
        content: '';
    } */

    .leftColumn {
        grid-column: 1;
        grid-row: 2;
    }

    .rightColumn {
        grid-column: 2;
        grid-row: 2;
    }
}

@media (min-width: 1200px) {
