@media (max-width: 480px) {
    .content {
        display: grid;
        grid-template-columns: repeat(2, 50vw);
        grid-template-rows: repeat(3, 135px);
        background-color: bisque;
        margin-top: 17px;
        margin-bottom: 17px;
        margin-left: auto;
        margin-right: auto
    }

    .content div:nth-child(n + 7) {
        display: none;
    }
}

@media (min-width: 480px) and (max-width: 768px) {
    .content {
        display: grid;
        grid-template-columns: repeat(2, 240px);
        grid-template-rows: repeat(3, 135px);
        background-color: bisque;
        margin-top: 17px;
        margin-bottom: 17px;
        margin-left: auto;
        margin-right: auto
    }

    .content div:nth-child(n + 7) {
        display: none;
    }
}

@media (min-width: 768px) and (max-width: 1025px) {
    .content {
        display: grid;
        grid-template-columns: repeat(3, 240px);
        grid-template-rows: repeat(3, 135px);
        background-color: bisque;
        margin-top: 17px;
        margin-bottom: 17px;
        margin-left: auto;
        margin-right: auto
    }

    .content div:nth-child(n + 10) {
        display: none;
    }

}

@media (min-width: 1025px) and (max-width: 1200px) {
    .content {
        display: grid;
        grid-template-columns: repeat(4, 240px);
        grid-template-rows: repeat(2, 135px);
        background-color: bisque;
        margin-top: 17px;
        margin-bottom: 17px;
        margin-left: auto;
        margin-right: auto
    }

    .content div:nth-child(n + 9) {
        display: none;
    }
}

@media (min-width: 1200px) {
    .content {
        display: grid;
        grid-template-columns: repeat(5, 240px);
        grid-template-rows: repeat(2, 135px);
        background-color: bisque;
        margin-top: 17px;
        margin-bottom: 17px;
        margin-left: -10px;
        margin-right: -10px
    }

}

.item img {
    display: grid;
    background-color: rgb(6, 6, 6);
    /* width: 100%;
    height: 100%;
    object-fit: cover; */
}