@media (max-width: 1200px) {
    .text {
        display: none;
    }

    .item {
        display: grid;
        width: 100%;
        margin-left: 0px;
        margin-right: 0px;
        text-transform: uppercase;
        align-items: center;
        width: 90px;
    }

    .item a {
        display: grid;
        align-content: center;
        justify-content: center;
        grid-column: 1/2;
        grid-row: 1;
        height: 90px;
        color: #000000;

    }

    .item:hover a,
    .item a.activeLink {
        color: #AE0018;
        fill: #AE0018;
    }

    .item:hover svg {
        fill: #AE0018;
    }

    .item svg {
        display: grid;
        width: 22px;
        height: 22px;
        margin-left: 15px;
        margin-right: 15px;
        align-self: center;
        grid-row: 1;
        grid-column: 2/3;
    }

    .item:hover .itemBorder {
        opacity: 1;
        transform: scaleX(1) scaleY(1);
    }

    .itemBorder,
    .itemBorderActive {
        display: grid;
        grid-row: 1;
        grid-column: 1/2;
        align-self: end;
        margin-left: 4px;
        margin-right: 4px;
        height: 3px;
        content: ' ';
        background-color: #AE0018;
        border-radius: 2px 2px 0 0;

    }

    .itemBorderActive {
        opacity: 1;
    }

    .itemBorder {
        opacity: 0;
        transition: opacity .2s ease-in-out, transform .2s ease-in-out;
        transform: scaleX(0.3) scaleY(0);
    }
}

