@media (max-width: 420px) {

    .btn {
        display: grid;
        grid-column: 1;
        grid-row: 2;
        justify-self: end;
        margin-top: 10px;
        align-self: end;
    }
}



@media (max-width: 767px) {
    .item {
        display: grid;
        justify-content: start;
        align-content: center;
        padding-left: 25px;
        margin-right: 25px;
        z-index: 2;
        text-transform: uppercase;
        line-height: 1.3;
        grid-column: 1;
        font-weight: 400;
        height: 100%;
        font-size: 32px;
        grid-row: 2/3;
        grid-template-columns: 2;
        grid-template-rows: 1;
    }

