.itemBox {
    display: grid;
    grid-template-rows: 222px 58px;
    width: 410px;
    box-shadow: 2px 2px 5px 2px #7a4e5488;
    border-radius: 18px 18px 6px 6px;

}

.img {
    display: grid;
    grid-row: 1;
    align-items: center;
    justify-items: center;

    z-index: 2;
    /* width: auto; */
    /* object-fit: contain; */
    overflow: hidden;
}


.img img {
    transition: width .2s, height .2s;
    height: 95%;
    width: 95%;
    /* padding: 10px; */
    /* z-index: 2; */
    /* object-fit: initial; */
    object-fit: contain;
    border-radius: 18px 18px 0px 0px;
}

.item {
    display: grid;
    align-content: center;
    justify-content: center;
    font-size: 20px;
    color: #fff;
    border-radius: 6px 6px 6px 6px;
}

@media (max-width: 480px) {
    .itemBox {
        display: grid;
        grid-template-rows: 222px 58px;
        width: 90vw;
        box-shadow: 2px 2px 5px 2px #7a4e5488;
        border-radius: 18px 18px 6px 6px;

    }

    .item {
        display: grid;
        align-content: center;
        justify-content: center;
        text-align: center;
    }
}

@media (max-width: 320px) {

    .item {
        text-align: center;
        font-size: 18px;
    }
}