@media (max-width: 1200px) {
    .content {
        display: none;
    }
}


@media (min-width: 1200px) {
    .content {
        font-size: 16px;
        font-weight: 300;
        list-style-type: none;
        display: grid;
        padding-inline-start: 10px;
        /* align-content: start; */
    }

    .main {
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 10px;
    }

    .herf {
        /* margin-left: 10px; */
        font-weight: 400;
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .herf a {
        color: #ffffff;
    }

    .herf:hover a {
        color: #ae0018;
    }
}

@media (min-width: 1440px) {

    .content {
        font-size: 16px;
        padding-inline-start: 40px;
    }

    .main {
        font-size: 20px;
    }
}