.itemHead {
    display: grid;
    /* justify-content: center; */
    text-transform: uppercase;
    /* margin-right: -10px;
    margin-left: -10px; */
    width: 100%;
    /* padding-left: 4px; */
}

@media (max-width: 480px) {
    .itemHead {
        display: grid;
        text-transform: uppercase;
        width: 100%;
        word-wrap: break-word;
        font-size: 32px;
    }
}

@media (max-width: 1200px) {
    .itemHeadBorder {
        height: 3px;
        background-color: #AE0018;
        width: auto;
        /* margin-left: -20px;
        padding-right: -20px; */
        border-radius: 2px 2px 0 0;
        margin-right: 4px;
        margin-left: 4px;
    }
}

@media (min-width: 1200px) {
    .itemHeadBorder {
        height: 3px;
        background-color: #AE0018;
        width: auto;
        border-radius: 2px 2px 0 0;
        margin-right: -4px;
        margin-left: -4px;
    }
}