.submitBtn {
    display: flex;
    padding: 5px 15px;
    border-radius: 6px;
    background-color: #000000;
    color: #ffffff;
    font-size: 16px;
    border: none;
    align-self: center;
}

.submitBtn:hover,
.submitBtn:active {
    transition: background-color .2s;
    background-color: #AE0018;
}