.content {
    display: grid;
    /* grid-row: 2fr 1fr; */
    text-decoration: none;
    margin-bottom: 17px;

}

.headerBox {
    display: flex;
    /* margin-top: 17px; */
    padding-top: 17px;
    padding-bottom: -6px;
    /* width: 1200px; */
    margin: auto;
    justify-self: center;
}

.itemBox {
    display: grid;
    /* grid-row: 2/3; */
    grid-template-columns: 1fr 1fr;
    margin-top: 19px;
    margin-bottom: 25px;
    grid-column-gap: 28px;
}

.itemBoxLeft {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: end;
    align-items: end;
}

.itemBoxRight {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: start;
    align-items: end;
}

.logo {
    margin-top: 17px;
    justify-self: center;
    height: 30px;
}

.slider {
    z-index: 5;
}

.contentBox {
    display: grid;
    justify-items: center;
    background-color: #eceef274;
    box-shadow: 2px 2px 3px #9090904b;
}

.textBox {
    display: grid;
    grid-template-columns: 80% 20%;
    align-items: start;
    justify-items: center;
    z-index: 1;
    padding-left: 25px;
    margin-bottom: 40px;
    margin-top: 40px;
    font-size: 26px;
    text-align: left;
}

@media (max-width: 480px) {
    .textBox {
        display: flex;

        /* grid-template-columns: 80% 20%; */
        flex-direction: column;
        justify-content: space-around;
        /* align-items: start; */
        /* justify-items: center; */
        z-index: 1;
        padding: 0 25px 0 25px;
        /* padding-left: 25px;
                padding-right: 25px; */
        margin: 30px 0 30px 0;
        /* margin-bottom: 40px;
                margin-top: 40px; */
        font-size: 22px;
        /* text-align: left; */
    }

    .textBoxBtn {
        margin-top: 30px;
    }
}

@media (min-width: 480px) and (max-width: 1200px) {

    .textBox {
        display: flex;

        /* grid-template-columns: 80% 20%; */
        flex-direction: column;
        justify-content: space-around;
        /* align-items: start; */
        /* justify-items: center; */
        z-index: 1;
        padding: 0 25px 0 25px;
        /* padding-left: 25px;
        padding-right: 25px; */
        margin: 40px 0 40px 0;
        /* margin-bottom: 40px;
        margin-top: 40px; */
        font-size: 26px;
        /* text-align: left; */
    }

    .textBoxBtn {
        margin-top: 40px;
    }
}