@media (max-width: 1200px) {
    .content {
        width: auto;
        margin: 17px 15px;
        /* margin-bottom: 34px; */
        display: grid;
        row-gap: 30px;
        text-align: center;
    }

    .box,
    .box_reverse {
        display: grid;
        display: flex;
        flex-direction: column;
        /* grid-template-columns: 1fr; */
        /* grid-template-rows: auto auto; */
        gap: 30px;
        align-items: center;
    }

    .box_reverse {
        flex-direction: column-reverse;
    }
}

@media (min-width: 1200px) {
    .content {
        width: 1180px;
        margin: 17px auto;
        /* margin-bottom: 34px; */
        display: grid;
        row-gap: 30px;
        text-align: center;
    }

    .box {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
        align-items: center;
    }
}

.boxMap {
    display: grid;
    justify-content: end;
}