@media (max-width: 420px) {
    .content {
        display: flex;
        flex-direction: column;
        background-color: #ECEEF2;
        height: 400px;
        box-shadow: 2px 2px 3px #90909083;

    }

    .contentText {
        display: flex;
        /* height: 160px; */
        margin-top: 30px;

    }

    .imgBox {
        display: flex;
        align-self: center;
        overflow: hidden;
        margin-top: 10px;
        z-index: 3;
        /* max-width: 90vh; */
    }
}

@media (min-width: 420px) and (max-width: 767px) {
    .content {
        display: flex;
        flex-direction: column;
        background-color: #ECEEF2;
        height: 400px;
        box-shadow: 2px 2px 3px #90909083;

    }

    .contentText {
        display: flex;
        /* height: 160px; */
        margin-top: 30px;

    }

    .imgBox {
        display: flex;
        align-self: center;
        overflow: hidden;
        margin-top: 30px;
        z-index: 3;
        /* max-width: 90vh; */
    }


}

@media (min-width: 768px) {
    .content {
        display: grid;
        grid-template-rows: 55% 45%;
        grid-template-columns: 40% 60%;
        background-color: #ECEEF2;
        height: 400px;
        grid-template-columns: 700px auto;
        box-shadow: 2px 2px 3px #90909083;
    }

    .contentText {
        display: grid;
        grid-column: 1/2;
        grid-row: 2/3;

    }

    .imgBox {
        display: grid;
        grid-column: 2/3;
        grid-row: 1/3;
        /* height: 400px; */
        height: 100%;
        width: auto;
        justify-content: start;
        align-content: start;
        overflow: hidden;
        z-index: 3;
    }
}


