    .content {
        display: grid;
        border-top: 2px solid #ECEEF2;
        border-bottom: 2px solid #ECEEF2;
        padding: 20px 0;
    }

    .formButtonBox {
        display: grid;
        justify-items: start;
        align-items: start;
        grid-column: 1/3;
    }

    .submitBtn {
        margin-top: 15px;
        margin-bottom: 15px;
        margin-left: 10px;
        padding: 10px 20px;
        border-radius: 6px;
        background-color: #AE0018;
        color: #ffffff;
        font-size: 16px;
        border: none;
    }

    .submitBtn:hover,
    .submitBtn:active {
        background-color: #AE0018;
    }



    .privacyPolicy a {
        color: #AE0018;
    }

    .leftColumn__header {
        display: grid;
        font-size: 30px;
        justify-self: start;
        align-self: start;
        padding-bottom: 7px;
        color: #AE0018;
        border-bottom: 2px solid #ECEEF2;
    }

    .rightColumn__row {
        display: grid;
        grid-column: 1/3;
        align-self: start;
    }

    .inputBox1,
    .inputBox2,
    .inputBox3 {
        display: grid;
        height: 41px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 10px;
        margin-bottom: 3px;
    }

    .inputBox3 {
        height: 142px;
    }

    .textArea1,
    .textArea2 {
        padding: 10px;
        resize: none;
        border: 1px solid #AE0018;
        border-radius: 6px;
        outline: none;
        height: 19px;
    }

    .textArea2 {
        height: 120px;
    }

    .textArea1:focus,
    .textArea2:focus,
    .textArea3:focus {
        background-color: #ECEEF2;
    }


    .leftColumn__text {
        display: grid;
        text-align: start;
        padding-top: 10px;
        font-size: 16px;
    }

    .leftColumn {
        display: grid;
        grid-column: 1;
    }

    .rightColumn {
        display: grid;
        align-items: start;
    }

    .privacyPolicy {
        display: grid;
        justify-items: start;
        align-items: center;
        padding-left: 10px;
        padding-bottom: 10px;
        font-size: 16px;
        /* background-color: aqua; */
    }

    @media (max-width: 767px) {

        .content {
            width: 100%;
            grid-template-rows: auto auto;
        }

        .leftColumn {
            margin-left: 10px;
            margin-bottom: 20px;
        }

        .rightColumn {
            grid-column: 1;
            margin-right: 20px;
            margin-left: 10px;
            grid-template-columns: 100%;
        }

        .leftColumn__text {
            display: none;
        }

        .inputBox1,
        .inputBox2,
        .inputBox3 {
            grid-column: 1;
        }

        .privacyPolicy {
            grid-column: 1/3;
        }
    }

    @media (min-width: 768px) and (max-width: 1025px) {
        .content {
            width: 100%;
            grid-template-rows: auto auto;
        }

        .leftColumn {
            margin-left: 10px;
            margin-bottom: 20px;
        }

        .rightColumn {
            grid-column: 1;
            margin-right: 20px;
            margin-left: 10px;
            grid-template-columns: 50% 50%;
            grid-template-rows: 54px auto auto;
            grid-auto-rows: auto;
        }

        .leftColumn__text {
            display: none;
        }

        .rightColumn__row {
            grid-column: 1/3;
        }

        .privacyPolicy {
            grid-column: 1/3;
        }
    }

    @media (min-width: 1025px) and (max-width: 1200px) {
        .content {
            width: 100%;
            /* width: auto; */
            margin: 0 auto;
            grid-template-columns: 35% 65%;
        }

        .leftColumn {
            margin-left: 10px;
            grid-template-rows: 15% 85%;
        }

        .rightColumn {
            margin-right: 10px;
            grid-column: 2;
            grid-template-columns: 50% 50%;
            grid-template-rows: 54px auto auto;
            grid-auto-rows: auto;
        }

        .rightColumn__row {
            grid-column: 1/3;
        }

        .privacyPolicy {
            grid-row: 4;
            grid-column: 1/3;
            width: 100%;
        }
    }

    @media (min-width: 1200px) {
        .content {
            margin: 0 auto;
            width: 1180px;
            grid-template-columns: 35% 65%;
        }

        .leftColumn {
            grid-template-rows: 15% 85%;
        }

        .rightColumn {
            grid-column: 2;
            grid-template-columns: 50% 50%;
            grid-template-rows: 54px auto auto;
            grid-auto-rows: auto;
        }

        .rightColumn__row {
            grid-column: 1/3;
        }

        .privacyPolicy {
            grid-row: 4;
            grid-column: 1/3;
        }
    }