@media (max-width: 1200px) {
    .content {
        width: 100%;
        margin: 17px auto;
        display: grid;
        grid-row: 2fr 1fr;
        text-align: center;
    }
}

@media (min-width: 1200px) {
    .content {
        width: 1180px;
        margin: 17px auto;
        display: grid;
        grid-row: 2fr 1fr;
        text-align: center;
    }
}