.footer {
    background-color: rgb(0, 0, 0);
    display: grid;
    padding-top: 7px;
}

@media (max-width: 480px) {
    .box {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 10% 1fr 1fr 1fr;
        align-content: center;
        height: 960px;
        margin-right: 15px;
        margin-left: 15px;
    }

    .large {
        display: none;
    }

    .item {
        display: grid;
        color: #ffffff;
        align-content: start;
    }

    .footer img {
        width: 245px;
        align-self: center;
        grid-column: 1;
        grid-row: 1;
    }

}


@media (min-width: 480px) and (max-width: 768px) {
    .box {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
