.disclaimer {
    display: grid;
    justify-content: center;
    align-content: end;
    color: #ffffff;
    margin-bottom: 10px;
    font-size: 16px;
    word-wrap: break-word;
    overflow-wrap: break-word;
    text-align: center;
}