    .phone,
    .email {
        color: #ffffff;
        text-decoration: none;
    }

    .phone:hover,
    .email:hover {
        color: #ae0018;
    }

    @media (max-width: 480px) {
        .content {
            font-size: 16px;
            font-weight: 300;
            list-style-type: none;
            display: grid;
            padding-inline-start: 10px;
            padding-right: 10px;
        }

        .main {
            font-size: 20px;
            font-weight: 500;
            margin-bottom: 10px;
        }
    }

    @media (min-width: 480px) and (max-width: 1200px) {
        .content {
            font-size: 14px;
            font-weight: 300;
            list-style-type: none;
            display: grid;
            padding-right: 20px;
            padding-inline-start: 10px;
        }


        .main {
            font-size: 18px;
            font-weight: 500;
            margin-bottom: 10px;
        }
    }

    @media (min-width: 1200px) {
        .content {
            font-size: 14px;
            font-weight: 300;
            list-style-type: none;
            display: grid;
            padding-inline-start: 10px;
        }


        .main {
            font-size: 18px;
            font-weight: 500;
            margin-bottom: 10px;
        }
    }

    @media (min-width: 1440px) {

        .content {
            font-size: 16px;
            padding-inline-start: 40px;
        }

        .main {
            font-size: 20px;
        }
    }